<template>
    <div class="main__week-item">
        <h2 class="main__week-item-title">{{ getWeekDay }}</h2>
        <p class="main__week-item-date">{{ getDay }} - {{ getMonth < 10 ? '0' + getMonth : getMonth }}</p>
                <div class="main__week-item-img">
                    <img :src="getImg" alt="">
                </div>
                <p class="main__week-item-temp">+20°</p>
                <p class="main__week-item-info">20</p>
    </div>
</template>
  
<script>
import { weatherName } from '@/icons'
import unix from '@/unix';

export default {
    props: {
        day: {
            typeof: Object
        }
    },
    computed: {
        desc() {
            return this.day.weather[0].description
        },
        getImg() {
            return weatherName[this.desc] || weatherName['ясно']
        },
        getDay() {
            return unix(this.day.dt, 'day')
        },
        getMonth() {
            return unix(this.day.dt, 'month')

        },
        getWeekDay() {
            return unix(this.day.dt, 'weekday')
        }
    }

}
</script>
  